html {
  position: relative;
  min-height: 100%;
  background: #1a1d21;
}

body {
  margin: 0;
  font-size: 1em;
  @media screen and (max-width: 800px) {
    font-size: 0.95em;
  }
  @media screen and (max-width: 700px) {
    font-size: 0.9em;
  }
}

* {
  font-family: "Roboto", sans-serif;
  color: #fff;
  font-weight: 300;
  letter-spacing: 0.06em;
}

a {
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
ul,
li,
p {
  margin: 0;
  padding: 0;
}

ul,
li {
  list-style: none;
}

label {
  display: block;
  margin: 15px 0px;
}

input,
textarea {
  width: 300px;
  display: block;
  color: #0c0d0f;
  border-radius: 4px;
  border: none;
  margin: 4px 0;
  padding: 6px;
  @media screen and (max-width: 500px) {
    width: 100%;
    padding: 6px 0;
  }
}

button {
  padding: 7px 14px;
  background: #2f3f56;
  font-size: 1em;
  font-weight: 400;
  border-radius: 6px;
  border: none;
  outline: none;
  cursor: pointer;
}

.app-container {
  width: 600px;
  margin: 0 auto;
  display: block;
}

.app-page {
  margin-bottom: 20px;
}

.main-container {
  padding: 20px 0;
}

.navbar {
  background: -webkit-linear-gradient(310deg, #16181b 50%, #111316 100%);
  background: -o-linear-gradient(310deg, #16181b 50%, #111316 100%);
  background: linear-gradient(140deg, #16181b 50%, #111316 100%);
  padding: 9px 0;
  font-size: 0.9em;
}
.navbar__container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.navbar__link {
  font-weight: 400;
  font-size: 0.95em;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  padding: 0 15px;
}
.navbar__link:first-child {
  padding-left: 0;
}
.navbar__link:last-child {
  padding-right: 0;
}

@media screen and (max-width: 640px) {
  .app-container {
    width: auto;
    padding: 0 20px;
  }
  .main-container {
    padding: 20px;
  }
}



